header {
  background-image: url(https://dev-static.hotelsforhope.com/ares/site_configs/f1_lv_gp_23-85764/img/banner.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center 45%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  justify-content: center;
  padding: 0 6rem;
}

.logo {
  grid-column: 2;
  text-align: center;
  min-width: 300px;
}

#races-contact {
  grid-column: 3;
  justify-self: end;
}

#races-contact li:hover {
  background: unset;
}

#races-contact a {
  display: inline-block;
  width: 100%;
}
#races-contact a:hover {
  color: #fc4c02;
}

header #races-contact {
  width: unset;
}

.ArnRateButton .ArnShowRatesLink {
  border-width: 2px;
  background: #008e97;
  border-color: #008e97;
  font-weight: bold;
}
.ArnRateButton .ArnShowRatesLink:hover {
  background: #fff;
  border-width: 2px;
  color: #008e97;
  border-color: #008e97;
}

@media only screen and (max-width: 400px) {
  header {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    justify-content: center;
    padding: 0 6rem;
  }

  header {
    grid-template-columns: unset;
    gap: unset;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    padding: initial;
  }

  .logo {
    grid-column: unset;
    text-align: initial;
    min-width: initial;
    width: 100%;
  }

  #races-contact {
    grid-column: unset;
    justify-self: initial;
  }

  #races-contact a {
    display: initial;
    width: initial;
  }

  header #races-contact {
    width: initial;
  }
}
